/* FLEX CONTAINER */
.flex-container {
  display: flex;
  flex-direction: row;
}

/* TODO APP */
.todo-app {
  max-width: 700px;
  margin: 0 auto;
  padding: .8em;
}

/* ADD TODO FORM */
.add-todo-form {
  margin: 0 auto;
  padding: 1em 0;
  max-width: 500px;
}

.add-todo-label {
  padding: 0;
  margin-right: .5em;
  flex-grow: 0;
}

.add-todo-input {
  border: 1px solid #d3d3d3;
  flex-grow: 1;
  padding-left: .5em;
  font-size: inherit;
}

.add-todo-button {
  color: white;
  background-color: #00D1B2;
  padding: 0 .4em;
  border: 1px solid #00D1B2;
  flex-grow: 0;
}

.add-todo-button:hover, 
.add-todo-button:focus {
  background-color: #008f7a;
  border-color: #008f7a;
}

.add-todo-button:disabled {
  background-color: #d8d8d8;
  border: 1px solid #d3d3d3;
  cursor: default;
}

/* TODO LIST */
.todo-list {
  padding: 1em 0;
}

/* TODO ITEM */
.todo-item {
  justify-content: space-between;
  padding: .2em 0;
}

.todo-item:nth-child(even) {
  background-color: #f0f0f0;
}

.todo-item-label,
.todo-item-input {
  margin-left: .5em;
}

.todo-item-input {
  font-size: inherit;
}

.todo-item-label.completed {
  text-decoration: line-through;
  color: #bebebe;
  font-style: italic;
}

.todo-item-buttons {
  margin-left: .3em;
}

.todo-item-button {
  padding: 0 .4em;
}

.edit-button {
  color: white;
  background-color: #174287;
  border: 2px solid transparent;
}

.edit-button:hover {
  color: #174287;
  background-color: white;
  border: 2px solid #174287;
}

.delete-button {
  color: white;
  background-color: #FF3860;
  border: 2px solid transparent;
}

.delete-button:hover {
  color: #FF3860;
  background-color: white;
  border: 2px solid #FF3860;
}